"use client";
import { useState, useRef, useEffect } from "react";
import WidgetLedger from "@/components/widget-ledger";
import TabSwitch from "@/components/tab-switch";
import { DashboardSummary } from "@/utils/store";
import { useDashboardStore } from "@/utils/store";

const dashboardAPIData: DashboardSummary = {
  totalBalance: 12345,
  totalCredits: 7890,
  totalDebits: 4455,
  transactionCount: 150,
  balanceChange: 5,
  creditsChange: 3,
  debitsChange: -2,
  transactionChange: 10,
};

export default function Dashboard() {
  const { dashboardSummary, setDashboardSummary } = useDashboardStore();

  // mimic api call to fetch data
  useEffect(() => {
    setTimeout(() => {
      setDashboardSummary(dashboardAPIData);
    }, 1000);
  }, []);

  return (
    <div className="">
      <WidgetLedger />
      <TabSwitch />
    </div>
  );
}
